html {
  font-family: "Noto Serif JP", serif;
  font-size: 0.5208333333vw;
}

body {
  overflow-x: hidden;
  font-family: "Noto Serif JP", serif;
}

body.fixed {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 0;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

picture {
  display: block;
}

a[href^="tel:"] {
  pointer-events: none;
}

@keyframes scroll-lead {
  0% {
    transform: scale(0, 1);
    transform-origin: 0 0;
  }

  50% {
    transform: scale(1, 1);
    transform-origin: 0 0;
  }

  51% {
    transform: scale(1, 1);
    transform-origin: 100% 0;
  }

  100% {
    transform: scale(0, 1);
    transform-origin: 100% 0;
  }
}

@keyframes bgextendAnimeBase {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes bgextendAnimeSecond {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes fadeUpAnime {
  from {
    opacity: 0;
    transform: translateY(100px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes rotateAnime {
  from {
    transform: rotateZ(0deg);
  }

  to {
    transform: rotateZ(360deg);
  }
}

@keyframes zoomUpAnime {
  0% {
    opacity: 1;
    transform: rotateZ(0deg) scale(1);
  }

  30% {
    opacity: 1;
    transform: rotateZ(360deg) scale(1);
  }

  to {
    opacity: 0;
    transform: rotateZ(360deg) scale(20);
  }
}

.about {
  width: 100%;
  padding: 160px 0;
  background-image: url("../images/about_background.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  overflow-x: hidden;
}

.about__inner {
  display: flex;
}

.about__left {
  width: 49.23%;
}

.about__title {
  width: 100%;
}

.about__text {
  width: 86%;
  margin-top: 60px;
}

.about__right {
  width: 49.23%;
  margin-left: 20px;
  position: relative;
}

.about__image {
  width: 129%;
  overflow: hidden;
  aspect-ratio: 830/560;
  position: absolute;
  top: 0;
  left: 0;
}

.about__image:after {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #3A863F;
}

.scroll-on .about__image:after {
  animation: scroll-lead 1s ease-in-out forwards;
}

.about__image img {
  height: auto;
  position: relative;
}

.about__image img:after {
  content: "";
  display: block;
  width: 286px;
  height: 286px;
  background-color: #3A863F;
  position: absolute;
  bottom: -20px;
  left: -20px;
}

.service {
  padding-top: 323px;
}

.service__inner {
  position: relative;
}

.service__items {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 60px 0;
}

.service__title {
  position: absolute;
  transform: translateY(-45%);
  left: -135px;
}

.news {
  margin-top: 160px;
}

#sbi_images {
  margin-top: 54px;
}

.sbi_images {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding: 5px;
}

.sbi_images .sbi_item {
  width: 258px;
  height: 258px;
  border: solid 5px #ffffff;
  background-color: #cccccc;
}

#sbi_load {
  display: flex;
  gap: 10px;
  justify-content: center;
  margin-top: 10px;
}

#sbi_load .sbi_load_btn {
  display: inline-block;
  padding: 7px 14px;
  background-color: #333333;
  color: #ffffff;
  font-size: 13px;
  color: #ffffff;
  font-weight: bold;
  line-height: 1.5;
  letter-spacing: 0;
  border-radius: 4px;
  text-align: center;
  font-family: "Noto Sans JP", sans-serif;
}

#sbi_load .sbi_follow_btn {
  display: inline-block;
  padding: 7px 14px;
  background-color: #408BD1;
  color: #ffffff;
  font-size: 13px;
  color: #ffffff;
  font-weight: bold;
  line-height: 1.5;
  letter-spacing: 0;
  border-radius: 4px;
  text-align: center;
  font-family: "Noto Sans JP", sans-serif;
}

.recruit-sec {
  width: 100%;
  margin-top: 265px;
}

.recruit-sec__bg {
  width: 100%;
  aspect-ratio: 3/1;
  background-image: url("../images/recruit-sec-bg.webp");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: relative;
}

.recruit-sec__inner {
  width: 100%;
  height: 100%;
}

.recruit-sec__title {
  letter-spacing: 0.33em;
  transform: translateY(-45%);
}

.recruit-sec__body {
  width: 1080px;
  margin: -210px auto 0;
  background-color: rgba(255, 255, 255, 0.8);
  padding: 72px 0 60px;
}

.recruit-sec__text {
  margin-top: 49px;
}

.recruit-sec__button {
  margin: 75px auto 0;
}

.page-link {
  margin-top: 120px;
}

.page-link__inner {
  border-top: solid 2px #3A863F;
  border-bottom: solid 2px #3A863F;
}

.button {
  height: 60px;
}

.button__color_orange {
  width: 240px;
  height: 60px;
  font-size: 16px;
  color: #ffffff;
  font-weight: 900;
  line-height: 1;
  letter-spacing: 0;
  text-transform: uppercase;
  background-color: #EC6A06;
  border-radius: 100vh;
  display: flex;
  align-items: center;
  filter: brightness(100%);
  transition: filter 0.3s ease;
}

.button__color_orange:hover {
  filter: brightness(110%);
  transition: filter 0.3s ease;
}

.button__color_orange a {
  width: 100%;
  text-align: center;
}

.button__color_white {
  width: 330px;
  height: 60px;
  font-size: 16px;
  color: #3A863F;
  font-weight: 900;
  line-height: 1;
  letter-spacing: 0;
  font-family: "Noto Sans JP", sans-serif;
  text-transform: uppercase;
  background-color: #ffffff;
  border-radius: 100vh;
  border: solid 2px #3A863F;
  display: flex;
  align-items: center;
  transition: all 0.3s ease;
}

.button__color_white:hover {
  color: #ffffff;
  background-color: #3A863F;
  transition: all 0.3s ease;
}

.button__color_white a {
  width: 100%;
  text-align: center;
}

.card01 {
  width: calc((100% - 40px) / 3);
}

.card01_w_wide {
  width: calc((100% - 20px) / 2);
}

.card01__image {
  width: 100%;
  overflow: hidden;
  aspect-ratio: 420/400;
}

.card01_w_wide .card01__image {
  aspect-ratio: 640/400;
}

.card01_w_wide .card01__image img {
  height: auto;
}

.card01__image img {
  height: auto;
}

.card01__body {
  padding: 26px 10px 19px;
  position: relative;
}

.card01__body:before {
  content: "";
  display: block;
  width: 180px;
  height: 7px;
  border-radius: 0 0 4px 4px;
  background-color: #3A863F;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.card01_w_wide .card01__body {
  padding: 26px 20px 19px;
}

.card01__title {
  font-size: 28px;
  color: #333333;
  font-weight: 900;
  line-height: 1;
  letter-spacing: 0.09em;
  text-align: center;
}

.card01__text {
  font-size: 16px;
  color: #333333;
  font-weight: 500;
  line-height: 1.5;
  letter-spacing: 0.09em;
  text-align: left;
  font-family: "Noto Sans JP", sans-serif;
  margin-top: 20px;
}

.section__title {
  font-size: 40px;
  color: #575757;
  font-weight: 900;
  line-height: 1;
  letter-spacing: 0.09em;
  text-align: center;
}

.section__title_line_2 {
  font-size: 40px;
  color: #575757;
  font-weight: 900;
  line-height: 2;
  letter-spacing: 0.09em;
}

.section__title_text_vertical {
  font-size: 60px;
  color: #575757;
  font-weight: 900;
  line-height: 1;
  letter-spacing: 0.4em;
  writing-mode: vertical-rl;
}

.section__title_text_vertical span {
  margin-right: 17px;
}

.section__title_lang_en {
  font-size: 16px;
  color: #3A863F;
  font-weight: 900;
  line-height: 1;
  letter-spacing: 0em;
  text-transform: uppercase;
  font-family: "Noto Sans JP", sans-serif;
  display: block;
  margin-top: 22px;
}

.head3__title {
  font-size: 40px;
  color: #575757;
  font-weight: 900;
  line-height: 1;
  letter-spacing: 0.09em;
}

.container {
  width: 1300px;
  margin: 0 auto;
}

.wide-container {
  width: 1600px;
  margin: 0 auto;
}

@media only screen and (max-width: 1600px) {
  .service {
    padding-top: 32.3rem;
  }

  .service__inner {
    width: 81.25%;
    margin: 0 auto;
  }

  .service__title {
    left: -13rem;
  }

  .card01 {
    width: calc((100% - 4rem) / 3);
  }

  .card01_w_wide {
    width: calc((100% - 2rem) / 2);
  }

  .card01__body {
    padding: 2.6rem 1rem 1.9rem;
  }

  .card01_w_wide .card01__body {
    padding: 2.6rem 2rem 1.9rem;
  }

  .card01__title {
    font-size: 2.8rem;
  }

  .card01__text {
    margin-top: 2rem;
  }

  .section__title_text_vertical {
    font-size: 6rem;
  }

  .section__title_text_vertical span {
    margin-right: 1.7rem;
  }

  .section__title_lang_en {
    margin-top: 2.2rem;
  }

  .wide-container {
    width: 94%;
  }
}

@media only screen and (max-width: 1300px) {
  html {
    font-size: 0.7692307692vw;
  }

  .about {
    padding: 16rem 0;
  }

  .about__text {
    margin-top: 6rem;
  }

  .about__right {
    margin-left: 6rem;
  }

  .service {
    padding-top: 16rem;
  }

  .service__items {
    margin-top: 6.4rem;
  }

  .service__title {
    position: static;
    transform: translateY(0%);
  }

  .recruit-sec {
    margin-top: 26.5rem;
  }

  .recruit-sec__title {
    transform: translateY(-160%);
  }

  .recruit-sec__body {
    width: 83.0769230769%;
    margin: -21rem auto 0;
    padding: 7.2rem 2rem 6rem;
  }

  .recruit-sec__text {
    margin-top: 4.9rem;
  }

  .recruit-sec__button {
    margin: 7.5rem auto 0;
  }

  .button__color_orange {
    width: 24rem;
    height: 6rem;
    font-size: 1.6rem;
  }

  .button__color_white {
    width: 33rem;
    height: 6rem;
    font-size: 1.6rem;
  }

  .card01__text {
    font-size: 1.6rem;
  }

  .section__title {
    font-size: 4rem;
  }

  .section__title_line_2 {
    font-size: 4rem;
  }

  .section__title_text_vertical {
    font-size: 4rem;
    writing-mode: horizontal-tb;
    text-align: center;
  }

  .section__title_text_vertical span {
    margin-right: 0;
  }

  .head3__title {
    font-size: 4rem;
  }

  .container {
    width: 94%;
  }
}

@media only screen and (max-width: 768px) {
  html {
    font-size: 1.3020833333vw;
  }

  .about {
    padding: 8rem 0;
  }

  .about__inner {
    flex-direction: column;
  }

  .about__left {
    width: 100%;
    display: contents;
  }

  .about__title {
    order: 1;
  }

  .about__text {
    width: 100%;
    order: 3;
    margin-top: 4rem;
  }

  .about__right {
    width: 100%;
    margin-left: 0;
    margin-top: 4rem;
    order: 2;
  }

  .about__image {
    width: 100%;
    height: auto;
    position: relative;
  }

  .service {
    padding-top: 8rem;
  }

  .service__items {
    gap: 4rem 0;
    margin-top: 4rem;
  }

  .recruit-sec__bg {
    aspect-ratio: 3/2;
  }

  .card01 {
    width: 100%;
  }

  .card01_w_wide {
    width: 100%;
  }

  .card01__image {
    aspect-ratio: 640/400;
  }

  .card01__body {
    padding: 2.6rem 0 1.9rem;
  }

  .card01_w_wide .card01__body {
    padding: 2.6rem 0 1.9rem;
  }

  .container {
    width: 91.1%;
  }

  .wide-container {
    width: 91.1%;
  }
}

@media only screen and (max-width: 560px) {
  a[href^="tel:"] {
    pointer-events: auto;
  }
}

@media only screen and (max-width: 428px) {
  html {
    font-size: 2.3364485981vw;
  }

  .recruit-sec__body {
    width: 91.1%;
    margin: -10rem auto 0;
    padding: 4rem 1.6rem;
  }

  .recruit-sec__text {
    margin-top: 4rem;
  }

  .recruit-sec__button {
    margin: 4rem auto 0;
  }

  .section__title {
    font-size: 2.8rem;
  }

  .section__title_line_2 {
    font-size: 2.8rem;
  }

  .section__title_text_vertical {
    font-size: 2.8rem;
  }

  .head3__title {
    font-size: 2.8rem;
  }
}